import { useEffect, useState } from 'react'

interface DashboardStats {
  totalUsers: number
  totalAssets: number
  activeAssets: number
  maintenanceAssets: number
  recentActivities: Array<{
    id: string
    type: string
    description: string
    timestamp: string
    user: string
  }>
}

interface Task {
  id: string
  title: string
  priority: "high" | "medium" | "low"
  status: "in_progress" | "todo" | "upcoming" | "completed"
  dueDate?: string
  description?: string
}

interface Project {
  id: string
  title: string
  description?: string
  progress: number
  status: "active" | "completed" | "on_hold"
  dueDate?: string
  teamMembers?: Array<{
    id: string
    name: string
    avatar?: string
  }>
  color?: string
}

interface CalendarEvent {
  id: string
  title: string
  time: string
  type: "meeting" | "reminder" | "deadline"
  location?: string
  isOnline?: boolean
  attendees?: number
}

